	onShow() {
		//初次登录状态
		var that = this
		uni.getStorage({
			key: 'Token',
			success: function(res) {
				if (!JSON.parse(res.data).Token) {
					uni.navigateTo({
						url: '/pages/Logo/Logo',
					});
					return false
				}
				console.log(JSON.parse(res.data))
				that.$store.state.CusId = JSON.parse(res.data).CusId
				that.$store.state.token = JSON.parse(res.data).Token
			}
		})
	},
	created() {

	},
	onNavigationBarButtonTap() {
		uni.scanCode({
			success: function(res) {
